$sm: 600px;
$md: 768px;
$lg: 992px;
$xl: 1200px;

@mixin phone {
  @media only screen and (max-width: $sm) {
    @content;
  }
}

@mixin large-phone {
  @media only screen and (min-width: $sm) {
    @content;
  }
}

@mixin tablet {
  @media only screen and (min-width: $md) {
    @content;
  }
}

@mixin laptop {
  @media only screen and (min-width: $lg) {
    @content;
  }
}

@mixin desktop {
  @media only screen and (min-width: $xl) {
    @content;
  }
}
